`, "",{"version":3,"sources":["webpack://./src///components/creation/Dice3D.vue"],"names":[],"mappings":"AAgGA,4BAAa,UAAW,CAAE,WAAY,CAAE,iBAAoB,CAC5D,4BAAa,UAAW,CAAE,WAAY,CAAE,iBAAkB,CAAE,2BAA4B,CAAE,6BAAgC,CAC1H,4BAAa,iBAAkB,CAAE,UAAW,CAAE,WAAY,CAAE,iCAAkC,CAAE,0BAA2B,CAAE,iBAAkB,CAAE,YAAa,CAAE,kBAAmB,CAAE,sBAAyB,CAC9M,yBAAU,0CAA6C,CACvD,yBAAU,4CAA6C,CAAE,qBAAsB,CAAE,OAAU,CAC3F,yBAAU,2CAA4C,CAAE,qBAAsB,CAAE,OAAU,CAC1F,yBAAU,4CAA6C,CAAE,YAAa,CAAE,6BAA8B,CAAE,OAAQ,CAAE,YAAe,CACjI,yBAAU,2CAA4C,CAAE,qBAAsB,CAAE,4BAA6B,CAAE,YAAe,CAC9H,yBAAU,4CAA6C,CAAE,YAAa,CAAE,6BAA8B,CAAE,OAAQ,CAAE,YAAa,CAAE,iBAAmB,CACpJ,sBAAO,UAAW,CAAE,WAAY,CAAE,kBAAmB,CAAE,iBAAkB,CAAE,0CAA6C","sourcesContent":["<template>\n  <div v-if=\"isVisible\" class=\"fixed inset-0 z-999999! flex flex-col items-center justify-center bg-black/80 text-white backdrop-blur-sm\">\n    <div class=\"mb-8 flex max-w-[600px] flex-wrap justify-center gap-8\">\n      <div v-for=\"(_, idx) in activeDice\" :key=\"idx\" :ref=\"el => diceRefs[idx] = el\" class=\"dice-icon\">\n        <div class=\"dice-cube\">\n          <div class=\"dice-face face-1\"><span class=\"pip\"></span></div>\n          <div class=\"dice-face face-2\"><span class=\"pip\"></span><span class=\"pip\"></span></div>\n          <div class=\"dice-face face-3\"><span class=\"pip\"></span><span class=\"pip\"></span><span class=\"pip\"></span></div>\n          <div class=\"dice-face face-4\"><span class=\"pip\"></span><span class=\"pip\"></span><span class=\"pip\"></span><span class=\"pip\"></span></div>\n          <div class=\"dice-face face-5\"><span class=\"pip\"></span><span class=\"pip\"></span><span class=\"pip\"></span><span class=\"pip\"></span><span class=\"pip\"></span></div>\n          <div class=\"dice-face face-6\"><span class=\"pip\"></span><span class=\"pip\"></span><span class=\"pip\"></span><span class=\"pip\"></span><span class=\"pip\"></span><span class=\"pip\"></span></div>\n        </div>\n      </div>\n    </div>\n    \n    <div class=\"h-12 text-center text-3xl font-bold text-yellow-400 drop-shadow-[0_0_12px_rgba(255,235,59,0.8)]\">{{ displayText }}</div>\n    \n    <button class=\"absolute bottom-8 rounded border border-white/50 bg-black/50 px-6 py-2 transition-colors hover:bg-black/80 hover:text-white\" @click=\"skip\">\n      \n    </button>\n  </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { gsap } from 'gsap';\nimport { nextTick, ref } from 'vue';\n\nconst isVisible = ref(false);\nconst activeDice = ref<number[]>([]);\nconst displayText = ref('');\nconst diceRefs = ref<any[]>([]);\nlet skipFlag = false;\nlet currentTimeline: gsap.core.Timeline | null = null;\n\nconst getRotationForFace = (face: number) => {\n  switch (face) {\n    case 1: return { x: 0, y: 0 };\n    case 6: return { x: 0, y: 180 };\n    case 3: return { x: 0, y: -90 };\n    case 4: return { x: 0, y: 90 };\n    case 2: return { x: -90, y: 0 };\n    case 5: return { x: 90, y: 0 };\n    default: return { x: 0, y: 0 };\n  }\n};\n\nconst roll = async (numDice: number, results: number[], loadingText: string): Promise<void> => {\n  isVisible.value = true;\n  skipFlag = false;\n  activeDice.value = Array(numDice).fill(0);\n  displayText.value = loadingText;\n  \n  await nextTick(); //  DOM \n  \n  return new Promise((resolve) => {\n    currentTimeline = gsap.timeline({\n      onComplete: () => resolve()\n    });\n\n    diceRefs.value.forEach((dieEl, i) => {\n      if (!dieEl) return;\n      const cube = dieEl.querySelector('.dice-cube');\n      const targetRot = getRotationForFace(results[i]);\n      \n      currentTimeline!.to(cube, {\n        rotationX: `+=${gsap.utils.random(720, 1080)}`,\n        rotationY: `+=${gsap.utils.random(720, 1080)}`,\n        duration: 1.5,\n        ease: \"power2.in\"\n      }, 0).to(cube, {\n        rotationX: targetRot.x,\n        rotationY: targetRot.y,\n        duration: 0.5,\n        ease: \"power4.out\"\n      });\n    });\n  });\n};\n\nconst updateText = async (text: string) => {\n  if (skipFlag) return;\n  displayText.value = text;\n  await new Promise(r => setTimeout(r, 1500));\n};\n\nconst hide = () => { isVisible.value = false; };\nconst skip = () => {\n  skipFlag = true;\n  if (currentTimeline) currentTimeline.progress(1);\n};\n\ndefineExpose({ roll, updateText, hide, skip });\n</script>\n\n<style scoped>\n/*  3D  CSS */\n.dice-icon { width: 5rem; height: 5rem; perspective: 800px; }\n.dice-cube { width: 100%; height: 100%; position: relative; transform-style: preserve-3d; transform: translateZ(-2.5rem); }\n.dice-face { position: absolute; width: 5rem; height: 5rem; background: rgba(255,255,255,0.98); border: 1.5px solid #857e72; border-radius: 12%; display: flex; align-items: center; justify-content: center; }\n.face-1 { transform: rotateY(0deg) translateZ(2.5rem); }\n.face-6 { transform: rotateY(180deg) translateZ(2.5rem); flex-direction: column; gap: 8px; }\n.face-3 { transform: rotateY(90deg) translateZ(2.5rem); flex-direction: column; gap: 4px; }\n.face-4 { transform: rotateY(-90deg) translateZ(2.5rem); display: grid; grid-template-columns: 1fr 1fr; gap: 8px; padding: 10px; }\n.face-2 { transform: rotateX(90deg) translateZ(2.5rem); flex-direction: column; justify-content: space-around; padding: 10px; }\n.face-5 { transform: rotateX(-90deg) translateZ(2.5rem); display: grid; grid-template-columns: 1fr 1fr; gap: 8px; padding: 10px; position: relative;}\n.pip { width: 14px; height: 14px; background: #3d352e; border-radius: 50%; box-shadow: inset 0 1px 3px rgba(0,0,0,0.5); }\n</style>"],"sourceRoot":""}]);
