word', name: '', price: 50, category: '', description: ' (1d8)' },\n  { id: 'short_bow', name: '', price: 40, category: '', description: ' (1d6)' },\n  { id: 'leather_armor', name: '', price: 30, category: '', armor_value: 3, description: '' },\n  { id: 'chain_mail', name: '', price: 85, category: '', armor_value: 5, description: '' },\n  { id: 'round_shield', name: '', price: 35, category: '', armor_value: 2, description: '' },\n  { id: 'padded_armor', name: '', price: 30, category: '', armor_value: 2, description: '' },\n  { id: 'cloth_armor', name: '', price: 15, category: '', armor_value: 1, description: '' },\n  { id: 'heal_potion', name: '', price: 10, category: '', description: ' 1d6 ' },\n  { id: 'arrows_10', name: ' (10)', price: 5, category: '' },\n  { id: 'rations_7', name: ' (7)', price: 14, category: '' },\n  { id: 'torch_6', name: ' (6)', price: 1, category: '' },\n  { id: 'adventurer_kit', name: '', price: 10, category: '', description: '//' }\n];\n\nconst equipmentSets: Record<string, any> = {\n  '()': { price: 95, items: ['short_sword', 'leather_armor', 'round_shield', 'heal_potion'] },\n  '()': { price: 90, items: ['long_sword', 'leather_armor', 'heal_potion'] },\n  '': { price: 99, items: ['short_bow', 'padded_armor', 'arrows_10', 'arrows_10', 'adventurer_kit'] },\n  '': { price: 90, items: ['short_sword', 'cloth_armor', 'round_shield', 'heal_potion', 'adventurer_kit'] },\n};\n\n// ---  ---\nconst form = reactive({\n  name: '',\n  gender: '',\n  age: '',\n  race: '',\n  gameMode: 'story',\n  ocDetails: '',\n  money: 100,\n  background: { originName: '', pastName: '', encounterName: '', originObj: null as any },\n  selectedClass: '',\n  attributes: { '': 0, '': 0, '': 0, '': 0, '': 0, '': 0, '': 0, '': 0 },\n  attributePool: 0,\n  equipment: [] as any[], // \n});\n\n// \nconst availableClasses = computed(() => {\n  const origin = form.background.originObj;\n  if (!origin || !origin.class) return [];\n  if (origin.class === 'any') return ['', '', '', '', '', '', '', ''];\n  if (Array.isArray(origin.class)) return origin.class;\n  return [origin.class];\n});\n\nconst uniqueSkills = computed(() => {\n  const skills: string[] = [];\n  // \n  if (raceData[form.race]?.skills) skills.push(...raceData[form.race].skills);\n  // \n  if (form.background.originObj?.skills) skills.push(...form.background.originObj.skills);\n  // \n  // ( past )\n  return [...new Set(skills)];\n});\n\nconst validationError = computed(() => {\n  if (!form.age) return '';\n  if (!form.background.originName) return '';\n  if (availableClasses.value.length > 0 && !form.selectedClass) return '';\n  if (form.attributes[''] === 0) return '';\n  if (form.attributePool > 0) return ` ${form.attributePool} `;\n  return '';\n});\n\n// ---  ---\nconst resetAttributes = () => {\n  // \n  Object.keys(form.attributes).forEach(k => form.attributes[k as keyof typeof form.attributes] = 0);\n  form.attributePool = 0;\n};\n\nconst roll1D6 = () => Math.floor(Math.random() * 6) + 1;\n// const roll1D3 = () => { const r = roll1D6(); return Math.ceil(r / 2); };\n\n// \nconst handleRollBackground = async () => {\n  const rolls = Array.from({ length: 6 }, roll1D6);\n  await diceRef.value?.roll(6, rolls, '...');\n\n  const s1 = rolls[0] + rolls[1];\n  const s2 = rolls[2] + rolls[3];\n  const s3 = rolls[4] + rolls[5];\n\n  const origin = backgroundData.origin[form.race]?.[s1] || backgroundData.origin[''][s1];\n  const past = backgroundData.past[s2];\n  const encounter = backgroundData.encounter[s3];\n\n  await diceRef.value?.updateText(`: ${origin.name}`);\n  await diceRef.value?.updateText(`: ${past.name}`);\n  await diceRef.value?.updateText(`: ${encounter}`);\n\n  form.background.originName = origin.name;\n  form.background.originObj = origin;\n  form.background.pastName = past.name;\n  form.background.encounterName = encounter;\n  form.selectedClass = ''; // \n\n  // \n  if (origin.money) {\n    const diceStr = origin.money; // e.g. '2d6*10'\n    const [d, m] = diceStr.split('*');\n    const [num, sides] = d.split('d').map(Number);\n    let total = 0;\n    for(let i=0; i<num; i++) total += Math.floor(Math.random()*sides)+1;\n    form.money = 100 + (total * (Number(m)||1));\n  } else {\n    form.money = 100;\n  }\n\n  diceRef.value?.hide();\n};\n\n// \nconst handleRollAttributes = async () => {\n  const config = raceData[form.race].attr_rolls; // e.g. { : '1d3+1', ... }\n  const rolls = []; // \n  let totalPoints = 0;\n  \n  // 7 (1d6d3)\n  for(let i=0; i<7; i++) rolls.push(roll1D6());\n  \n  await diceRef.value?.roll(7, rolls, '...');\n  \n  let i = 0;\n  for (const key in config) {\n    if (key === '') continue;\n    const formula = config[key]; // '1d3+1'\n    const mod = parseInt(formula.split('+')[1] || formula.split('-')[1] || '0');\n    const isMinus = formula.includes('-');\n    const d6 = rolls[i++];\n    const d3 = Math.ceil(d6/2);\n    const val = isMinus ? Math.max(1, d3 - mod) : d3 + mod;\n    \n    form.attributes[key as keyof typeof form.attributes] = 1; // 1\n    totalPoints += val; // \n  }\n\n  //  (1-3)\n  const pot = Math.floor(Math.random() * 3) + 1;\n  form.attributes[''] = pot;\n\n  //  ( - 71)\n  form.attributePool = totalPoints - 7;\n\n  await diceRef.value?.updateText(` ${totalPoints} `);\n  diceRef.value?.hide();\n};\n\n// \nconst buyItem = (item: any) => {\n  if (form.money >= item.price) {\n    form.money -= item.price;\n    form.equipment.push({...item}); // \n  }\n};\nconst sellItem = (index: number) => {\n  const item = form.equipment[index];\n  form.money += item.price;\n  form.equipment.splice(index, 1);\n};\nconst buySet = (setName: string) => {\n  const set = equipmentSets[setName];\n  if (form.money >= set.price) {\n    form.money -= set.price;\n    set.items.forEach((id: string) => {\n      const it = equipmentData.find(x => x.id === id);\n      if(it) form.equipment.push({...it});\n    });\n  }\n};\n\n// \nconst nextStep = () => { if(currentStep.value < 3) currentStep.value++; }\nconst prevStep = () => { if(currentStep.value > 0) currentStep.value--; }\n\n// \nconst generateSummary = () => {\n  const attrStr = Object.entries(form.attributes).map(([k,v]) => `${k}:${v}`).join(', ');\n  const equipStr = form.equipment.map(i => i.name).join(', ') || '';\n  \n  return `===  ===\n: ${form.name || '{{user}}'} (${form.race} ${form.gender} ${form.age})\n: ${form.background.originName} / ${form.background.pastName} / ${form.background.encounterName}\n: ${form.selectedClass || ''}\n: ${attrStr}\n: ${uniqueSkills.value.join(', ')}\n: ${form.money} \n: ${equipStr}\nOC: ${form.ocDetails || ''}\n: ${form.gameMode === 'story' ? '' : ''}`;\n};\n\n// \nconst submit = async () => {\n  if (validationError.value) return;\n  \n  const finalName = form.name.trim() || '{{user}}';\n  const jobRecord = form.selectedClass ? `\"${form.selectedClass}\": { \"\": 1, \"\": 10, \"\": 0, \"\": 1000 }` : '';\n  \n  // \n  const bagMap: Record<string, any> = { '':{}, '':{}, '':{}, '':{}, '':{} };\n  form.equipment.forEach(item => {\n    const cat = item.category || '';\n    const target = bagMap[cat] || bagMap[''];\n    if (!target[item.name]) {\n      target[item.name] = { \n        name: item.name, \n        type: item.category, \n        description: item.description, \n        quantity: 0,\n        armor_value: item.armor_value || 0\n      };\n    }\n    target[item.name].quantity++;\n  });\n\n  //  Slash  ()\n  const slashCommand = `/send <START>\\\\nAdmin:\\\\nTRPG User \\\\n\\\\n# \\\\n/\\\\n\\\\n# \\\\n---\\\\n## \\\\n- **:** ${finalName}\\\\n- **:** ${form.gender}\\\\n- **:** ${form.age}\\\\n- **:** ${form.race}\\\\n\\\\n## \\\\n- **:** ${form.background.originName}\\\\n- **:** ${form.background.pastName}\\\\n- **:** ${form.background.encounterName}\\\\n\\\\n## \\\\n- **:** ${form.selectedClass}\\\\n- **:** ${uniqueSkills.value.join(', ')}\\\\n\\\\n##  (OC)\\\\n- **:** ${form.ocDetails || ''}\\\\n\\\\n## \\\\n- **:** ${form.attributes['']}\\\\n- **:** ${form.attributes['']}\\\\n- **:** ${form.attributes['']}\\\\n- **:** ${form.attributes['']}\\\\n- **:** ${form.attributes['']}\\\\n- **:** ${form.attributes['']}\\\\n- **:** ${form.attributes['']}\\\\n- **:** ${form.attributes['']}\\\\n\\\\n## \\\\n- **:** ${form.money} \\\\n- **:** ${form.equipment.map(i=>i.name).join(', ')}\\\\n\\\\n<UpdateVariable>\n    // ==  ==\n    _.set('.', '${finalName}');\n    _.set('.', '${form.race}');\n    _.set('.', 1);\n    _.set('.', 0);\n\n    // ==  ==\n    _.set('.', {\n      : ${form.attributes['']},\n      : ${form.attributes['']},\n      : ${form.attributes['']},\n      : ${form.attributes['']},\n      : ${form.attributes['']},\n      : ${form.attributes['']},\n      : ${form.attributes['']},\n    });\n    \n    // ==  () ==\n    _.set('..', ${10 + form.attributes[''] * 5 + form.attributes[''] * 2});\n    _.set('..', ${10 + form.attributes[''] * 5 + form.attributes[''] * 2});\n    _.set('..', ${5 + form.attributes[''] + form.attributes['']});\n    _.set('..', ${5 + form.attributes[''] + form.attributes['']});\n    _.set('..', ${form.attributes['']});\n    _.set('..', ${form.attributes['']});\n    _.set('..', ${form.attributes['']});\n    _.set('..', ${form.attributes['']});\n    _.set('..', 0);\n    _.set('..', 0);\n\n    // ==  ==\n    _.set('.', { ${jobRecord} });\n    \n    // ==  ==\n    _.set('.', ${JSON.stringify(bagMap)});\n    _.set('..', { :0, :${form.money}, :0 });\n\n  </UpdateVariable>\\\\n\\\\|\\\\n/trigger`;\n\n  try {\n    const api = ST_API as any;\n    if (api.triggerSlash) {\n      await api.triggerSlash(slashCommand);\n      emit('created');\n    } else {\n      console.warn(\"API Unavailable, copying to clipboard\");\n      navigator.clipboard.writeText(slashCommand);\n      alert(\"API\");\n    }\n  } catch(e) {\n    console.error(e);\n  }\n};\n</script>\n\n<style scoped>\n/*  */\n.animate-fade-in {\n  animation: fadeIn 0.4s ease-out;\n}\n@keyframes fadeIn {\n  from { opacity: 0; transform: translateY(10px); }\n  to { opacity: 1; transform: translateY(0); }\n}\n</style>"],"sourceRoot":""}]);
