`, "",{"version":3,"sources":["webpack://./src///components/IntroSequence.vue"],"names":[],"mappings":"AA8TA,kCAEE,2BACF","sourcesContent":["<template>\n  <div class=\"fixed inset-0 z-99999 flex items-center justify-center overflow-hidden bg-[#030303] font-serif text-white select-none\">\n    <!--  -->\n    <div class=\"pointer-events-none absolute inset-0 opacity-40 mix-blend-screen\">\n      <div class=\"absolute inset-0 animate-pulse bg-[radial-gradient(ellipse_at_center,rgba(60,40,90,0.4)_0%,transparent_60%)] opacity-60 duration-10000\"></div>\n      <div class=\"absolute inset-0 animate-[spin_120s_linear_infinite] bg-[url('https://www.transparenttextures.com/patterns/stardust.png')] opacity-30\"></div>\n      <div class=\"absolute inset-0 animate-[spin_180s_linear_infinite_reverse] bg-[url('https://www.transparenttextures.com/patterns/stardust.png')] opacity-15\"></div>\n    </div>\n    \n    <!--  (Vignette)  -->\n    <div class=\"pointer-events-none absolute inset-0 z-10 bg-[radial-gradient(circle_at_center,transparent_20%,#000_100%)]\"></div>\n\n    <!--  -->\n    <div class=\"relative z-20 flex h-full w-full items-center justify-center overflow-hidden perspective-[1500px]\">\n      <div ref=\"stage\" class=\"relative flex h-full w-full max-w-7xl transform-gpu items-center justify-center px-10 text-center\">\n      <!--  GSAP  -->\n      <div v-for=\"(scene, sIndex) in creditsSequence\" :key=\"'scene-'+sIndex\" class=\"scene-container pointer-events-none absolute inset-0 flex flex-col items-center justify-center opacity-0\">\n        \n        <!--  () -  -->\n        <div class=\"role-text mb-8 transform-gpu font-serif text-xs font-bold tracking-[0.8em] text-[#a89f91] uppercase drop-shadow-2xl md:mb-14 md:text-xl\">\n          {{ scene.role }}\n        </div>\n\n        <!--  -->\n        <div class=\"names-container flex flex-wrap items-center justify-center gap-x-12 gap-y-6 md:gap-x-24 md:gap-y-12\">\n          <div\n            v-for=\"(name, nIndex) in scene.names\"\n            :key=\"'name-'+sIndex+'-'+nIndex\"\n            class=\"name-group inline-flex transform-gpu\"\n            :data-style=\"scene.style\"\n          >\n            <!--  -->\n            <span \n              v-for=\"(char, cIndex) in name.split('')\" \n              :key=\"cIndex\"\n              class=\"name-char inline-block transform-gpu text-4xl font-black transition-colors sm:text-5xl md:text-6xl lg:text-[5.5rem]\"\n              :style=\"{ \n                color: scene.color, \n                textShadow: `0 0 15px ${scene.color}66, 0 0 30px ${scene.color}33`,\n                marginRight: char === ' ' ? '0.4em' : '0' \n              }\"\n            >\n              {{ char === ' ' ? '' : char }}\n            </span>\n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n\n  <!--  -  -->\n    <button\n      class=\"group absolute right-8 bottom-8 z-30 cursor-pointer overflow-hidden rounded border border-white/20 bg-black/40 px-6 py-2 text-[10px] tracking-[0.3em] text-white/50 uppercase backdrop-blur-md transition-all duration-700 hover:border-white/60 hover:bg-white/10 hover:text-white hover:shadow-[0_0_15px_rgba(255,255,255,0.2)]\"\n      @click=\"skip\"\n    >\n      <span class=\"relative z-10 block transition-transform group-hover:scale-110\">[ Skip Sequence ]</span>\n      <div class=\"absolute inset-0 -translate-x-full bg-linear-to-r from-transparent via-white/10 to-transparent transition-transform duration-700 group-hover:translate-x-full\"></div>\n    </button>\n\n    <!--  -  -->\n    <div class=\"absolute bottom-0 left-0 z-30 h-[2px] w-full bg-white/5\">\n      <div ref=\"progressBar\" class=\"relative h-full w-0 bg-linear-to-r from-transparent via-[#c59d5f] to-[#ffdeb3] shadow-[0_0_10px_#c59d5f]\">\n        <!--  -->\n         <div class=\"absolute top-1/2 right-0 h-[4px] w-[15px] -translate-y-1/2 bg-white blur-[2px]\"></div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { gsap } from 'gsap';\nimport { nextTick, onBeforeUnmount, onMounted, ref } from 'vue';\n\nconst emit = defineEmits<{\n  (e: 'complete'): void\n}>();\n\nconst stage = ref<HTMLElement | null>(null);\nconst progressBar = ref<HTMLElement | null>(null);\nlet masterTl: gsap.core.Timeline | null = null;\n\n// \nconst creditsSequence = [\n  { role: 'ORIGINAL AUTHOR', names: ['Elainades'], color: '#FFD700', style: 'hero' },\n  {\n    role: 'CORE ARCHITECTS & LOGIC',\n    names: ['Keil', '11', 'KKKKKK_0735', 'Sulfurzero', 'USS Essex CV-9'],\n    color: '#00F0FF',\n    style: 'grid',\n  },\n  { role: 'VISUALS & AESTHETICS', names: ['', '', ''], color: '#FF0055', style: 'glitch' },\n  {\n    role: 'WORLD BUILDERS & SCENARIO',\n    names: ['Italian Pasta', '', 'K1nn', 'S15', 'Gemini114514'],\n    color: '#50C878',\n    style: 'cloud',\n  },\n  {\n    role: 'CHARACTERS & STORIES',\n    names: ['Glen0822', '', '', '', 'Shiyue137', 'Tttk2929'],\n    color: '#E0B0FF',\n    style: 'cloud',\n  },\n  {\n    role: 'SPECIAL THANKS',\n    names: ['', 'Hsuika', '', '', 'AA', 'Boss', 'Shin'],\n    color: '#FFA500',\n    style: 'cloud',\n  },\n  { role: 'VALHALLA (LEGACY)', names: ['Qwerty', 'LunaGlaze'], color: '#808080', style: 'fade' },\n  { role: 'PRESENTED BY', names: ['THE FOUR DICE WORLD', 'ZOD EDITION'], color: '#ffffff', style: 'final' },\n];\n\n// ---  ---\nconst playSequence = async () => {\n  if (!stage.value) return;\n\n  // Ensure Vue has fully rendered the elements before querying\n  await nextTick();\n\n  const scenes = stage.value.querySelectorAll('.scene-container');\n\n  masterTl = gsap.timeline({\n    onComplete: finish,\n    onUpdate: function() {\n      if (progressBar.value) {\n        gsap.set(progressBar.value, { width: `${this.progress() * 100}%` });\n      }\n    }\n  });\n\n  scenes.forEach((sceneEl, index) => {\n    const roleText = sceneEl.querySelector('.role-text');\n    const nameGroups = sceneEl.querySelectorAll('.name-group');\n    const nameChars = sceneEl.querySelectorAll('.name-char'); // \n\n    // \n    const styleType = nameGroups[0]?.getAttribute('data-style') || 'fade';\n    \n    // \n    const sceneTl = gsap.timeline();\n\n    // \n    sceneTl.set(sceneEl, { opacity: 1, pointerEvents: 'auto' });\n    \n    // --- 1 () ---\n    //  3D /\n    //  stage \n    if (index % 2 === 0) {\n      sceneTl.fromTo(stage.value, \n        { rotationY: -5, rotationX: 2, z: -100 }, \n        { rotationY: 5, rotationX: -2, z: 150, duration: 4.5, ease: 'sine.inOut' }, \n        0\n      );\n    } else {\n      sceneTl.fromTo(stage.value, \n        { rotationY: 5, rotationX: -2, z: -150 }, \n        { rotationY: -5, rotationX: 2, z: 100, duration: 4.5, ease: 'sine.inOut' }, \n        0\n      );\n    }\n    \n    // --- 2 ---\n    //  + \n    sceneTl.fromTo(roleText,\n      { opacity: 0, y: 40, filter: 'blur(15px)', letterSpacing: '0.2em', scale: 0.9 },\n      { opacity: 1, y: 0, filter: 'blur(0px)', letterSpacing: '0.8em', scale: 1, duration: 1.5, ease: 'expo.out' },\n      0.1\n    );\n\n    // --- 3 ---\n    if (styleType === 'hero') {\n      // Hero:  Z \n      sceneTl.fromTo(\n        nameChars,\n        { opacity: 0, scale: 4, z: 800, filter: 'blur(30px)', rotationX: () => gsap.utils.random(-80, 80), rotationY: () => gsap.utils.random(-80, 80) },\n        { opacity: 1, scale: 1, z: 0, filter: 'blur(0px)', rotationX: 0, rotationY: 0, duration: 1.8, stagger: { amount: 0.8, from: 'random' }, ease: 'power4.out' },\n        0.4\n      );\n      \n    } else if (styleType === 'grid') {\n      // Grid: \n      sceneTl.fromTo(\n        nameGroups,\n        { opacity: 0, y: 120, rotationX: -90, filter: 'contrast(2) brightness(0)' },\n        { opacity: 1, y: 0, rotationX: 0, filter: 'contrast(1) brightness(1)', duration: 1.2, stagger: 0.15, ease: 'back.out(2)' },\n        0.4\n      );\n      // \n      sceneTl.fromTo(nameChars,\n        { color: '#ffffff', textShadow: '0 0 40px #fff' },\n        { color: '', textShadow: '', duration: 0.5, stagger: 0.05, ease: 'power2.out' },\n        0.5\n      );\n      \n    } else if (styleType === 'glitch') {\n      // Glitch: \n      sceneTl.fromTo(nameGroups,\n        { opacity: 0, scale: 1.05, filter: 'blur(20px)' },\n        { opacity: 1, scale: 1, filter: 'blur(0px)', duration: 0.8, ease: 'power2.inOut' },\n        0.3\n      );\n      sceneTl.fromTo(\n        nameChars,\n        { opacity: 0, x: () => gsap.utils.random(-50, 50), skewX: () => gsap.utils.random(-40, 40) },\n        { opacity: 1, x: 0, skewX: 0, duration: 0.2, stagger: { amount: 0.4, from: 'random' } },\n        0.4\n      );\n      // \n      sceneTl.to(nameChars, {\n        duration: 0.1,\n        skewX: () => gsap.utils.random(-15, 15),\n        x: () => gsap.utils.random(-5, 5),\n        filter: 'hue-rotate(90deg) brightness(2)',\n        repeat: 3,\n        yoyo: true,\n        stagger: 0.02\n      }, 1.2);\n      \n    } else if (styleType === 'cloud') {\n      // Cloud: \n      sceneTl.fromTo(\n        nameChars,\n        { opacity: 0, y: 50, filter: 'blur(20px) contrast(0.5)', scale: 0.9 },\n        { opacity: 1, y: 0, filter: 'blur(0px) contrast(1)', scale: 1, duration: 2.5, stagger: { amount: 1.5, from: 'edges' }, ease: 'sine.out' },\n        0.3\n      );\n      \n    } else if (styleType === 'final') {\n      // Final: \n      sceneTl.fromTo(\n        nameGroups,\n        { opacity: 0, scale: 0.8, filter: 'blur(25px) brightness(3)', y: 20 },\n        { opacity: 1, scale: 1, filter: 'blur(0px) brightness(1)', y: 0, duration: 3.5, stagger: 0.6, ease: 'power2.out' },\n        0.4\n      );\n      // \n      sceneTl.fromTo(nameChars, \n        { letterSpacing: '0.5em' }, \n        { letterSpacing: 'normal', duration: 4, ease: 'power3.out' }, \n        0.4\n      );\n      \n    } else {\n      //  Fade: \n      sceneTl.fromTo(\n        nameChars,\n        { opacity: 0, filter: 'blur(10px)', rotationZ: () => gsap.utils.random(-15, 15), scale: 0.8 },\n        { opacity: 1, filter: 'blur(0px)', rotationZ: 0, scale: 1, duration: 1.8, stagger: { amount: 1.2, from: 'random' }, ease: 'power2.out' },\n        0.4\n      );\n    }\n\n    // --- 4 (Hold) ---\n    // \n    const holdDuration = styleType === 'final' ? 4.5 : 2.5;\n    sceneTl.to({}, { duration: holdDuration });\n\n    // --- 5 (Dissolve Exit) ---\n    const exitDuration = 1.0;\n    \n    // \n    sceneTl.to(roleText, {\n      opacity: 0,\n      y: -30,\n      filter: 'blur(15px)',\n      letterSpacing: '0.4em',\n      duration: exitDuration,\n      ease: 'power2.in',\n    });\n    \n    // \n    const exitTargets = nameChars.length && styleType !== 'grid' && styleType !== 'final' ? nameChars : nameGroups;\n    \n    sceneTl.to(exitTargets, {\n      opacity: 0,\n      z: 400, // \n      filter: 'blur(20px)',\n      scale: 1.2,\n      duration: exitDuration + 0.2,\n      stagger: { amount: 0.3, from: 'center' },\n      ease: 'power2.in',\n    }, `<`);\n\n    // \n    sceneTl.set(sceneEl, { opacity: 0, pointerEvents: 'none' });\n\n    // --- 6 ---\n    // ** 0.8 \n    // \n    const overlapObj = index === 0 ? '+=0' : '-=0.8';\n    masterTl!.add(sceneTl, overlapObj);\n  });\n};\n\nconst finish = () => {\n  if (masterTl) masterTl.kill();\n  gsap.to(stage.value, { scale: 1.5, opacity: 0, duration: 1, onComplete: () => emit('complete') });\n};\n\nconst skip = () => {\n  if (masterTl) masterTl.kill();\n  emit('complete');\n};\n\nonMounted(() => {\n  // Add slight delay to ensure DOM is ready and fonts loaded\n  setTimeout(() => {\n    playSequence();\n  }, 100);\n});\n\nonBeforeUnmount(() => {\n  if (masterTl) masterTl.kill();\n});\n</script>\n\n<style scoped>\n.scene-container {\n  /* Ensuring scenes perfectly overlap in the center */\n  transform-style: preserve-3d;\n}\n</style>\n"],"sourceRoot":""}]);
